import React from 'react';
import { Link } from 'react-router-dom';
import Box from '@material-ui/core/Box';
import Grid from '@material-ui/core/Grid';
import { ThemeProvider } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Button from '@material-ui/core/Button';

import Videos from '../../videos.json';
import thumbSample from '../../contents/sample/thumbnail.png';
import thumbYoga from '../../contents/yoga/thumbnail.png';
import thumbBts from '../../contents/btsDynamite/thumbnail.png';
import thumbBtsShort from '../../contents/btsDynamiteShort/thumbnail.png';
import thumbBaseball from '../../contents/baseball/thumbnail.png';
import thumbBox from '../../contents/box/thumbnail.png';
import thumbNiziU from '../../contents/niziu/thumbnail.png';
import thumbPaprika from '../../contents/paprika/thumbnail.png';
import thumbEbikani from '../../contents/ebikani/thumbnail.png';
import { darktheme, bkbackground, titletext, linktext } from '../../styles';

const selectVideos = () => {
  const homeUrl = process.env.PUBLIC_URL;
  const videosData = Videos.data;
  const thumbs = [];
  for (let i in videosData) {
    if (videosData[i]['datapath'] == './contents/sample') {
      thumbs.push(`${thumbSample}`);
    } else if (videosData[i]['datapath'] == './contents/yoga') {
      thumbs.push(`${thumbYoga}`);
    } else if (videosData[i]['datapath'] == './contents/btsDynamite') {
      thumbs.push(`${thumbBts}`);
    } else if (videosData[i]['datapath'] == './contents/btsDynamiteShort') {
      thumbs.push(`${thumbBtsShort}`);
    } else if (videosData[i]['datapath'] == './contents/baseball') {
      thumbs.push(`${thumbBaseball}`);
    } else if (videosData[i]['datapath'] == './contents/box') {
      thumbs.push(`${thumbBox}`);
    } else if (videosData[i]['datapath'] == './contents/niziu') {
      thumbs.push(`${thumbNiziU}`);
    } else if (videosData[i]['datapath'] == './contents/paprika') {
      thumbs.push(`${thumbPaprika}`);
    } else if (videosData[i]['datapath'] == './contents/ebikani') {
      thumbs.push(`${thumbEbikani}`);
    }
  }
  console.log(thumbs);
  return (
    <section style={bkbackground}>
      <ThemeProvider theme={darktheme}>
        <AppBar position="static">
          <Toolbar>
            <Link to={homeUrl + `/`} style={linktext}>
              <Button color="inherit">BACK</Button>
            </Link>
          </Toolbar>
        </AppBar>
        <Grid container>
          <Grid item xs={12}>
            <Box my={3} style={titletext} textAlign="center">
              Select Video
            </Box>
          </Grid>
          {videosData.map((val, i) => {
            return (
              <Grid key={i} item xs={12} sm={6} md={6} lg={4}>
                <Box mx={3} textAlign="center">
                  <Link to={homeUrl + videosData[i]['path']}>
                    <img
                      src={thumbs[i]}
                      alt={videosData[i]['title']}
                      style={{
                        'max-width': '100%',
                        height: 'auto',
                      }}
                    />
                  </Link>
                </Box>
                <Box color="white" textAlign="center" mb={3}>
                  {videosData[i]['title']}
                </Box>
              </Grid>
            );
          })}
        </Grid>
      </ThemeProvider>
    </section>
  );
};

export default selectVideos;
