/* eslint-disable react/prop-types */
import React, { Component } from 'react';
import { Player, ControlBar, BigPlayButton } from 'video-react';
import 'video-react/dist/video-react.css';

import video_sample from '../contents/sample/video.mp4';
import video_yoga from '../contents/yoga/video.mp4';
import video_bts from '../contents/btsDynamite/video.mp4';
import video_bts_short from '../contents/btsDynamiteShort/video.mp4';
import video_baseball from '../contents/baseball/video.mp4';
import video_box from '../contents/box/video.mp4';
import video_niziu from '../contents/niziu/video.mp4';
import video_paprika from '../contents/paprika/video.mp4';
import video_ebikani from '../contents/ebikani/video.mp4';
import posterSample from '../contents/sample/poster.png';
import posterYoga from '../contents/yoga/poster.png';
import posterBts from '../contents/btsDynamite/poster.png';
import posterBtsShort from '../contents/btsDynamiteShort/poster.png';
import posterBaseball from '../contents/baseball/poster.png';
import posterBox from '../contents/box/poster.png';
import posterNiziu from '../contents/niziu/poster.png';
import posterPaprika from '../contents/paprika/poster.png';
import posterEbikani from '../contents/ebikani/poster.png';

class VideoPlayer extends Component {
  constructor(props, context) {
    super(props, context);
    this.state = {
      currentTime: 0,
      ended: false,
      count: 0,
    };
    if (this.props.path == './contents/sample') {
      this.state.videoSrc = `${video_sample}`;
      this.state.posterSrc = `${posterSample}`;
    } else if (this.props.path == './contents/yoga') {
      this.state.videoSrc = `${video_yoga}`;
      this.state.posterSrc = `${posterYoga}`;
    } else if (this.props.path == './contents/btsDynamite') {
      this.state.videoSrc = `${video_bts}`;
      this.state.posterSrc = `${posterBts}`;
    } else if (this.props.path == './contents/btsDynamiteShort') {
      this.state.videoSrc = `${video_bts_short}`;
      this.state.posterSrc = `${posterBtsShort}`;
    } else if (this.props.path == './contents/baseball') {
      this.state.videoSrc = `${video_baseball}`;
      this.state.posterSrc = `${posterBaseball}`;
    } else if (this.props.path == './contents/box') {
      this.state.videoSrc = `${video_box}`;
      this.state.posterSrc = `${posterBox}`;
    } else if (this.props.path == './contents/niziu') {
      this.state.videoSrc = `${video_niziu}`;
      this.state.posterSrc = `${posterNiziu}`;
    } else if (this.props.path == './contents/paprika') {
      this.state.videoSrc = `${video_paprika}`;
      this.state.posterSrc = `${posterPaprika}`;
    } else if (this.props.path == './contents/ebikani') {
      this.state.videoSrc = `${video_ebikani}`;
      this.state.posterSrc = `${posterEbikani}`;
    }
    this.play = this.play.bind(this);
    this.getCurrentTime = this.getCurrentTime.bind(this);
    this.getState = this.getState.bind(this);
  }

  componentDidMount() {
    // subscribe state change
    this.player.subscribeToStateChange(this.handleStateChange.bind(this));
  }

  handleStateChange(state) {
    // copy player state to this component's state
    this.setState({
      player: state,
    });
  }

  play() {
    this.player.playbackRate = this.props.speed;
    this.player.play();
  }

  getCurrentTime() {
    const { player } = this.player.getState();
    this.setState({ currentTime: player.currentTime });
    this.props.setCurrentTime(this.state.currentTime);
  }

  getState() {
    const { player } = this.player.getState();
    this.setState({ ended: player.ended });
    this.props.setPlayerState(this.state.ended);
  }

  componentDidUpdate() {
    this.player.subscribeToStateChange(this.getState);
    if (this.props.play) {
      if (this.state.count === 0) {
        this.play();
        this.setState({ count: this.state.count + 1 });
      }
    }
  }

  render() {
    return (
      <div>
        <Player
          ref={(player) => {
            this.player = player;
          }}
          // autoPlay
          playsInline={true}
          poster={this.state.posterSrc}
        >
          <source src={this.state.videoSrc} type="video/mp4" />
          <BigPlayButton disabled />
          <ControlBar disabled />
        </Player>
      </div>
    );
  }
}

export default VideoPlayer;
