/* eslint-disable react/prop-types */
import React, { useState } from 'react';
import Webcam from 'react-webcam';
import { Link } from 'react-router-dom';
import { useWindowWidth } from '@react-hook/window-size';
import * as poseDetection from '@tensorflow-models/pose-detection';
import '@tensorflow/tfjs-backend-webgl';
import Box from '@material-ui/core/Box';
import Grid from '@material-ui/core/Grid';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Slide from '@material-ui/core/Slide';
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Switch from '@material-ui/core/Switch';
import { ThemeProvider } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import FormControl from '@material-ui/core/FormControl';
import NativeSelect from '@material-ui/core/NativeSelect';
import InputLabel from '@material-ui/core/InputLabel';

import Player from '../../components/player';
import calcHu from '../../utils/calcHu';
import { drawKeypoints, drawSkeleton } from '../../utils/utilities';
import videosData from '../../videos.json';
import { bkbackground } from '../../styles';
import { darktheme, linktext } from '../../styles';

export const MoveLink = (props) => {
  const [score, setScore] = useState(0);
  const [playerState, setPlayerState] = useState({});
  const [play, setPlay] = useState(false);
  const [count, setCount] = useState(0);
  const [open, setOpen] = useState(false);
  const [totalScore, setTotalScore] = useState(0);
  const [perfectScore, setPerfectScore] = useState(0);
  const [buttonFlag, setButtonFlag] = useState(true);
  const [buttonName, setButtonName] = useState('Loading....');
  const [checked, setChecked] = useState(true);
  const [caution, setCaution] = useState("Let's Link and Move!");
  const [cautionColor, setCautionColor] = useState('white');
  const [playSpeed, setPlaySpeed] = useState({
    speed: 1.0,
    name: '',
  });
  const playRef = React.useRef(null);
  const startTimeRef = React.useRef(0);
  const playSpeedRef = React.useRef(1);
  const webcamRef = React.useRef(null);
  const canvasRef = React.useRef(null);

  const homeUrl = process.env.PUBLIC_URL;
  const dataPath = props.path.replace('./', '') + '/analyzed.json';
  const Data = require('../../../src/' + dataPath);
  const onlyWidth = useWindowWidth();
  let aa = 2.3;
  if (onlyWidth < 576) {
    aa = 1.4;
  }
  const width = parseInt(onlyWidth / aa, 10);

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
    window.location.reload();
  };

  const handleSelectChange = (event) => {
    const name = event.target.name;
    setPlaySpeed({
      ...playSpeed,
      [name]: event.target.value,
    });
  };

  const toLinkEN = () => {
    window.open('https://jp.surveymonkey.com/r/7M8ZQHM', '_blank');
  };

  const toLinkJP = () => {
    window.open(
      'https://docs.google.com/forms/d/e/1FAIpQLSfw8BdHlcJkOo89JXWewGEjvnuNrJzkCsZUeYFNRj8tIYKFqQ/viewform?usp=sf_link',
      '_blank'
    );
  };

  const handleChange = (event) => {
    setChecked(event.target.checked);
  };

  let pose = [{ keypoints: [], score: 0 }];
  let refSize = Data.size;
  let framerate = Data.fps;
  const videoData = videosData.data;
  const start = videoData[props.index].start;
  const end = videoData[props.index].end;

  const th = 0.3;
  let similarity = 0;
  let keypoints = [];
  const videoConstraints = {
    width: refSize[0],
    height: refSize[1],
  };

  const searchFrame = () => {
    let flag = true;
    let now = performance.now();
    console.log(playSpeedRef.current);
    if (startTimeRef.current == 0) {
      flag = false;
    }
    let currentTime_ = (now - startTimeRef.current) * playSpeedRef.current;
    let targetFrame = Math.round((currentTime_ * framerate) / 1000);
    if (currentTime_ > end) {
      flag = false;
    } else if (currentTime_ < start) {
      flag = false;
    }
    console.log(currentTime_, targetFrame);
    return [targetFrame, flag];
  };

  const detectWebCamFeed = async (detector) => {
    if (
      typeof webcamRef.current !== 'undefined' &&
      webcamRef.current !== null &&
      webcamRef.current.video.readyState === 4
    ) {
      const video = webcamRef.current.video;
      const videoWidth = webcamRef.current.video.videoWidth;
      const videoHeight = webcamRef.current.video.videoHeight;
      webcamRef.current.video.width = videoWidth;
      webcamRef.current.video.height = videoHeight;
      pose = await detector.estimatePoses(video);
      setButtonFlag(false);
      setButtonName('START !!!!');
      if (playRef.current) {
        setCaution("Let's Link and Move!");
        setCautionColor('white');
        calcSimilarity(pose, videoWidth, videoHeight);
      } else {
        diagnose(pose);
      }

      drawResult(pose, video, videoWidth, videoHeight, canvasRef);
    }
  };

  const runMovenet = async () => {
    const detectorConfig = {
      modelType: poseDetection.movenet.modelType.SINGLEPOSE_LIGHTNING,
    };
    const detector = await poseDetection.createDetector(
      poseDetection.SupportedModels.MoveNet,
      detectorConfig
    );

    setInterval(() => {
      detectWebCamFeed(detector);
    }, 100);
  };

  React.useEffect(() => {
    runMovenet();
  }, []);

  const drawResult = (pose, video, videoWidth, videoHeight, canvas) => {
    const ctx = canvas.current.getContext('2d');
    canvas.current.width = videoWidth;
    canvas.current.height = videoHeight;
    drawKeypoints(pose[0]['keypoints'], 0.3, ctx, videoWidth);
    drawSkeleton(pose[0]['keypoints'], 0.3, ctx, videoWidth);
  };

  const calc_matchshape = (hu1, hu2, mode = 3) => {
    let s = 0;
    hu1.forEach((elem, i) => {
      let mA = hu1[i];
      let mB = hu2[i];
      if (mode == 1) {
        s += Math.abs(1 / mA - 1 / mB);
      } else if (mode == 2) {
        s += Math.abs(mA - mB);
      } else if (mode == 3) {
        s += Math.abs((mA - mB) / mA);
      }
    });
    return s;
  };

  const calcSimilarity = (pose) => {
    let [targetFrame, flag] = searchFrame();

    if (flag) {
      let Hu1 = [...Data.data[targetFrame]['nyu']];

      const videoWidth = webcamRef.current.video.videoWidth;
      const videoHeight = webcamRef.current.video.videoHeight;
      //realtime pose
      keypoints = pose[0]['keypoints'];
      let Hu2 = calcHu(keypoints, videoWidth, videoHeight);
      console.log([Hu1, Hu2]);
      similarity = Math.exp(-calc_matchshape(Hu2, Hu1));
      setScore(similarity);
      console.log(similarity);
    } else {
      setScore(0);
    }
  };

  const diagnose = (pose) => {
    keypoints = pose[0]['keypoints'];
    let ln = keypoints.length;
    let diag = 0;
    for (let i = 0; i < ln; i++) {
      if (keypoints[i]['score'] < th) {
        diag = 1;
        setCaution('Please show the whole body');
        setCautionColor('red');
        break;
      }
    }
    if (diag == 0) {
      setCaution('Camera OK!');
      setCautionColor('aqua');
    }
  };

  React.useEffect(() => {
    if (count < 2) {
      if (count == 1) {
        startTimeRef.current = performance.now();
        console.log(startTimeRef.current);
      }
      setCount(count + 1);
    }
    playRef.current = play;
  }, [play]);

  const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
  });

  React.useEffect(() => {
    if (playerState) {
      if (count === 2) {
        setPlay(false);
        handleOpen();
        setCount(count + 1);
      }
    }
  }, [playerState]);

  React.useEffect(() => {
    setTotalScore(totalScore + score);
    setPerfectScore(perfectScore + 1);
  }, [score]);

  React.useEffect(() => {
    playSpeedRef.current = playSpeed.speed;
  }, [playSpeed.speed]);

  return (
    <section style={bkbackground}>
      <ThemeProvider theme={darktheme}>
        <AppBar position="static">
          <Toolbar>
            <Link to={homeUrl + `/selectvideos`} style={linktext}>
              <Button color="inherit">SELECT VIDEO</Button>
            </Link>
          </Toolbar>
        </AppBar>
        <Grid container alignItems="center" justify="center">
          <Grid item xs={12}>
            <Box
              my={1}
              style={{
                color: cautionColor,
                fontSize: '3vmax',
                fontWeight: 'normal',
              }}
              textAlign="center"
            >
              {caution}
            </Box>
          </Grid>
          <Grid item xs={12}>
            <Box my={1} textAlign="center">
              <Button
                variant="contained"
                disabled={buttonFlag}
                color="primary"
                size="large"
                onClick={() => {
                  setPlay(true);
                }}
              >
                {buttonName}
              </Button>
            </Box>
          </Grid>
          <Grid item xs={12}>
            <Box mx={5} my={1} bgcolor={'rgba(255,255,0,' + score + ')'}>
              <Grid container alignItems="center" justify="center">
                <Grid item xs={12} sm={6} md={6} lg={6}>
                  <Box mx={2} my={1} textAlign="center">
                    <Webcam
                      mirrored={checked}
                      ref={webcamRef}
                      videoConstraints={videoConstraints}
                      style={{
                        position: 'absolute',
                        verticalAlign: 'middle',
                        width: width,
                        height: 'auto',
                      }}
                    />
                    <canvas
                      ref={canvasRef}
                      style={{
                        position: 'relative',
                        verticalAlign: 'middle',
                        width: width,
                        height: width,
                      }}
                    />
                  </Box>
                </Grid>
                <Grid item xs={12} sm={6} md={6} lg={6}>
                  <Box
                    mx={2}
                    mu={1}
                    textAlign="center"
                    style={{ 'pointer-events': 'none' }}
                  >
                    <Player
                      setPlayerState={setPlayerState}
                      play={play}
                      path={props.path}
                      speed={playSpeed.speed}
                    />
                    {/* {playerSelect(props.path)} */}
                  </Box>
                </Grid>
              </Grid>
            </Box>
            <Grid container alignItems="center" justify="center">
              <Grid item xs={4}>
                <Box ml={8} textAlign="center">
                  <FormGroup>
                    <FormControlLabel
                      control={
                        <Switch
                          color="primary"
                          checked={checked}
                          onChange={handleChange}
                        />
                      }
                      label="Mirror"
                      style={linktext}
                    />
                  </FormGroup>
                </Box>
              </Grid>
              <Grid item xs={8}>
                <Box mr={8} my={0} textAlign="right">
                  {/* <Typography style={linktext}>Play Speed</Typography> */}
                  <FormControl style={{ minWidth: 100 }}>
                    <InputLabel shrink htmlFor="speed-label">
                      Play Speed
                    </InputLabel>
                    <NativeSelect
                      value={playSpeed.speed}
                      onChange={handleSelectChange}
                      name="speed"
                      inputProps={{
                        'speed-label': 'speed',
                      }}
                    >
                      <option value={0.5}>x0.5</option>
                      <option value={0.75}>x0.75</option>
                      <option value={1.0}>x1.0</option>
                      <option value={1.25}>x1.25</option>
                      <option value={1.5}>x1.5</option>
                    </NativeSelect>
                  </FormControl>
                </Box>
              </Grid>
            </Grid>
            <Dialog
              open={open}
              TransitionComponent={Transition}
              keepMounted
              onClose={handleClose}
              aria-labelledby="goodjob"
              aria-describedby="greatwork"
            >
              <DialogTitle id="goodjob">{'Good Job!!!'}</DialogTitle>
              <DialogContent>
                <DialogContentText id="greatwork">
                  Your Score is{' '}
                  {Math.round((totalScore / perfectScore) * 10000) / 100} /100
                </DialogContentText>
              </DialogContent>
              <DialogActions>
                <Button
                  variant="contained"
                  onClick={handleClose}
                  color="primary"
                  size="large"
                >
                  RETRY
                </Button>
                <Link to={homeUrl + `/selectvideos`} style={linktext}>
                  <Button variant="contained" color="primary" size="large">
                    SELECT VIDEO
                  </Button>
                </Link>
              </DialogActions>
              <DialogContent>
                <DialogContentText id="survey">
                  <br />
                  <br />
                  Please help us fill out the survey !!!
                </DialogContentText>
              </DialogContent>
              <DialogActions>
                <Button variant="contained" onClick={toLinkJP}>
                  アンケート (日本語)
                </Button>
              </DialogActions>
              <DialogActions>
                <Button variant="contained" onClick={toLinkEN}>
                  Survey Link (English)
                </Button>
              </DialogActions>
            </Dialog>
          </Grid>
        </Grid>
      </ThemeProvider>
    </section>
  );
};
