import React from 'react';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import login from '../pages/login/login';
import selectVideos from '../pages/selectVideos/selectVideos';
import { MoveLink } from '../pages/exercise/exercise';
import Videos from '../videos.json';

const App = () => {
  const homeUrl = process.env.PUBLIC_URL;
  const videosData = Videos.data;
  return (
    <BrowserRouter>
      <Switch>
        <Route exact path={homeUrl + '/'} component={login} />
        <Route
          exact
          path={homeUrl + '/selectvideos'}
          component={selectVideos}
        />
        {videosData.map((val, i) => {
          return (
            <Route
              key={i}
              exact
              path={homeUrl + videosData[i]['path']}
              render={() => (
                <MoveLink path={videosData[i]['datapath']} index={i} />
              )}
            />
          );
        })}
      </Switch>
    </BrowserRouter>
  );
};

export default App;
